<template>
<div>
    <div class="main-search-input-inner">
        <div class="left-arrow-wrap" @click="goBackHandle">
            <span class="left-arrow-bg"></span>
        </div>
        <div class="middle-input-wrap">
            <input type="text" :value="keywords.label" placeholder="搜地点、监控等..." @click="handleClickSelect"/>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: 'MainSearchInput',
    props:{
        keywords: {
          type: Object,
          default: () => {
            return {
              label: '',
              value: 0
            }
          }
        }
    },
    methods: {
      handleClickSelect() {
        // 打开搜索详情页面
        this.$root.eventHub.$emit('showSearchDetailEvent')
      },
      // 返回
      goBackHandle() {
        this.$root.eventHub.$emit('hideSearchDetailEvent',{
            label: '',
            value: 0
        })
      }
    }
}
</script>

