<template>
  <div id="app">
    <!--百度地图容器-->
    <baidu-map-container :isExpend="isExpend" :keywords="keywords"></baidu-map-container>
    <!--搜索展开页面-->
    <search-detail-page v-if="showDtail"></search-detail-page>
    <!--主搜索框-->
    <main-search-input class="main-search-input-wrap" :keywords="keywords"></main-search-input>
  </div>
</template>

<script>
// 地图容器
import BaiduMapContainer from './components/BaiduMapContainer.vue'
// 搜索展开页面
import SearchDetailPage from './components/SearchDetailPage.vue'
// 主走索框
import MainSearchInput from  './components/MainSearchInput.vue'

export default {
  name: 'app',
  data() {
    return {
      // 是否显示搜索页面（覆盖地图）
      showDtail: false,
      // 搜索框input值
      keywords: {
        label: '',
        value: 0
      },
      // 地图是否是展开状态(收缩的时候，占屏幕一半，下方用于展示图元列表)
      isExpend: true
    }
  },
  components: {
    BaiduMapContainer,
    SearchDetailPage,
    MainSearchInput
  },
  mounted() {
    // console.log(this.$root)
    this.$root.eventHub.$on('showSearchDetailEvent', () => {
      this.showDtail = true
    })
    this.$root.eventHub.$on('hideSearchDetailEvent', data => {
      if (data) {
        this.keywords = data
        if(data.label) {
          this.isExpend = false
        }
      } 
      // 如果当前搜索详情页面已打开
      if(this.showDtail) {
        this.showDtail = !this.showDtail
        return
      } else {
        // 输入框中的值重置为空
        // 展开地图
        this.isExpend = true
        // 同native交互，返回上一个native页面
        console.log('同native交互，返回上一个native页面')
      }
    })
  }
}
</script>

<style lang="scss" scoped>
@import './assets/sass/mixin.scss';
#app{
  height:100%;
  position:relative;
}
.main-search-input-wrap{
  position:absolute;
  left:0;
  @include px2rem(top, 59);
  width:100%;
}
</style>
