<script>
function renderMap() {
// 修改BMap中请求地图瓦片数据地址
  // console.log(BMap)
  // BMap.apiUrl = 'http://192.168.3.240:19965/'
  // BMap.searchUrl = 'http://192.168.3.240:19965/'
