  var map = new BMap.Map("container")
  var point = new BMap.Point(114.31629058749178, 30.558978501783265)
  map.centerAndZoom(point, 15)
  map.enableScrollWheelZoom(true)
}
// 是否是发布环境
const BMapUrl = 'https://api.map.baidu.com/api?v=2.0&ak=eXoOXW4BgauUt8mygEmsuv51wLynxRwF'
// const BMapUrl = 'http://192.168.3.240:19976/baidumap/jsapi/api.js'
//加载js脚本
function loadScript(url, cb) {
  var script = document.createElement('script');
  script.async = true,
      script.src = url;

  var entry = document.getElementsByTagName('script')[0];
  entry.parentNode.insertBefore(script, entry);

  script.onload = script.onreadystatechange = function () {
      var rdyState = script.readyState;

      if (!rdyState || /complete|loaded/.test(script.readyState)) {
          cb();
          script.onload = null;
          script.onreadystatechange = null;
      }
  }
}

